/**
 *
 * Table Style (SCSS)
 *
 * @author Takuto Yanagida
 * @version 2021-01-15
 *
 */


@mixin scroll {
	@include stile-table-pseudo-scroll-bar;
}

@mixin stile-table-pseudo-scroll-bar {
	div[data-stile ~= 'fixed-table-scroll-bar'] {
		position  : fixed;
		bottom    : 0;
		z-index   : 99;
		margin    : 0;
		overflow-x: auto;
		overflow-y: hidden;  // for IE11 & Edge

		height: calc(1rem + 2px);  // for Safari
		border: 1px solid rgba(transparent, 0.1);  // for Safari

		display: none;
		@media print { display: none !important; }

		> div {  // Spacer
			height: 1px;
		}
	}
}
